import {createSelector} from 'reselect';
import { usersAPI } from '../.vs/src/api/api';



const getUsersSelector = (state) => {
    return state.usersPage.users;
}




export const getUsersWithStatus=(state) => {
       
        const users=state.usersPage.users;

        state.usersPage.usersWithStatus=users.filter(u => u.status != null );
        return state.usersPage.usersWithStatus;

}






    

    export const getFollowedUsers = createSelector (getUsersSelector,
       
            (users) => {
        return users.filter(u => u.followed == false);
     
        })




//sra mej ogtagorcum enq verevi grvac parz selector@,
// karox enq mi qani parz selectorner evs ogtagorcel sra mej
export const getUsers = createSelector (getUsersSelector,
//users@ ayn e inch@ veradardznum e getUsersSelector parz selector@ 
    (users) => {
return users.filter(u => true);
//sa nuyn bann e veradardznum inch getUsersSelector@, uxxaki grel enq vorpes orinak
})



