import React, {useState, useEffect} from 'react';
import Iframe from 'react-iframe';
import Preloader from '../../common/Preloader/Preloader';
import ProfileMusicForm from './ProfileMusic';
import { saveMusicSuccess1, saveMusicSuccess2, saveMusicSuccess3 } from '../../../Redux/profile-reducer';
import { withAuthRedirect } from '../../Dialogs/hoc/withAuthRedirect';
import { compose } from 'redux';
import {connect} from 'react-redux';
import s from '../ProfileMusic/ProfileMusicContainer.module.css';
import userPhoto from '../../../.vs/src/assect/images/user.png';


const ProfileMusic = (props) => {


let [editMode, setEditeMode] = useState(true);




  if(!props.isAuth) {
  return <Preloader />
  }
  
  
 

    const activateEditeMode = () => {

      setEditeMode(true);

    }

 const deactivateEditeMode = () => {

      setEditeMode(false);

    }

 

   return    (


       <div> 
         
  {editMode  && <ProfileMusicPage goToEditMode = {deactivateEditeMode} music1={props.music1}  music2={props.music2}  music3={props.music3} 
  isAuth={props.isAuth } deactivateEditeMode={props.deactivateEditeMode}/>}

{!editMode &&

     <div>
<ProfileMusicForm  saveMusicSuccess1={props.saveMusicSuccess1}
 saveMusicSuccess2={props.saveMusicSuccess2} 
 saveMusicSuccess3={props.saveMusicSuccess3} activateEditeMode={activateEditeMode} />

 </div>

 }

       
       

      </div>

    

   )
   
}







const ProfileMusicPage= (props) => {






return <div>

<div> 
 <h2> Here is you favorite music!!!</h2>
</div>


{props.isAuth && <div> <button onClick = {props.goToEditMode}> Edit </button> </div>
}






<div > 
 <b> 1. </b>
</div>



<div>
<Iframe width="500" height="150" src='https://www.youtube.com/watch?v=FxFNprPOdss'  
position="relative">
</Iframe>
</div>


<div> 
  <b>2.</b>  
<Iframe width="500" height="150" src={props.music2}
></Iframe>
</div>


<div > 
 <b>3. </b>  
<Iframe width="500" height="150" src={props.music3}
></Iframe>
</div>



</div>


}


  let mapStateToProps = (state) => ({

  profile: state.profilePage.profile,
  music1: state.profilePage.music1,
  music2: state.profilePage.music2,
  music3: state.profilePage.music3,
  isAuth: state.auth.isAuth

  });
 
 
 
export default compose (
    connect(mapStateToProps, {saveMusicSuccess1, saveMusicSuccess2, saveMusicSuccess3}),
    withAuthRedirect)(ProfileMusic); 






