import React from 'react';
import StatusUsers from './statusUsers';


import {connect} from 'react-redux';
import {setCurrentPage,  
      requestUsersWithStatus,requestUsers} from '../../Redux/users-reducer';

import {getStatusUsers, 
    getCurrentPage,    
       getPageSize,
        getTotalUsersCount, getUsers, getUsersWithStatus, getUsersCountWithStatus, getPageSizeStatus
       } from '../../Redux/users-selectors';
import { compose } from 'redux';


class StatusUsersContainer extends React.Component {


   componentDidMount () {
      

      this.props.requestUsersWithStatus(this.props.currentPage, this.props.pageSizeStatus);
      
      this.props.requestUsers(this.props.currentPage,this.props.pageSize);
   
   }

   
  onPageChanged = (pageNumber) => {


   this.props.requestUsersWithStatus(pageNumber, this.props.pageSizeStatus);


  }


render () {

   return  <StatusUsers users={this.props.usersWithStatus} 
            totalUsersCount={this.props.totalUsersCount}
            pageSize={this.props.pageSizeStatus}
            currentPage= {this.props.currentPage}
            onPageChanged= {this.onPageChanged} />
   
      
}

}


let  mapStateToProps = (state) => {

   return {
   
        totalUsersCount: getTotalUsersCount(state),
        usersWithStatus: state.usersPage.usersWithStatus,
        pageSizeStatus: getPageSizeStatus(state),
        //usersCountWithStatus:state.usersPage.usersCountWithStatus,
        currentPage:  getCurrentPage(state)
        
     
   }
   
}
   
export default connect (mapStateToProps, {requestUsersWithStatus, setCurrentPage, requestUsers}, ) (StatusUsersContainer);