import React from 'react';
import {connect} from 'react-redux';
import { follow, unfollow, setUsers, setCurrentPage, 
    setTotalUsersCount, toggleIsFetching,
     toggleFollowingProgress,
      requestFollowedUsers} from '../../Redux/users-reducer';
import * as axios from 'axios';
import Users from '../Users/Users';
import Preloader from '../common/Preloader/Preloader';
import { compose } from 'redux';
import {getCurrentPage,
     getFollowingInProgress,
      getIsFetching,
       getPageSize,
        getFollowedUsersCount,
        getUsers, getFollowedUsers} from '../../Redux/users-selectors';
import { Redirect } from 'react-router-dom';
import {isAuth} from '../../Redux/auth-reducer';
import { withAuthRedirect } from '../Dialogs/hoc/withAuthRedirect';
import s from './FriendsContainer.module.css';

class FriendsContainer extends React.PureComponent {
 

    componentDidMount () {

 this.props.requestFollowedUsers(this.props.currentPage, this.props.pageSize);
 
    }
  
  
  onPageChanged = (pageNumber) => {


      this.props.requestFollowedUsers(pageNumber, this.props.pageSize);
 

     }
   

  
  

     
      render () {
           
            return <>
            {this.props.isFetching ?  <Preloader /> : null}
            <div className= {s.friends}>Followed Users</div>
             <Users totalUsersCount= {this.props.followedUsersCount}
                          pageSize= {this.props.pageSize}
                          currentPage= {this.props.currentPage}
                          onPageChanged= {this.onPageChanged}
                          users={this.props.users}                         
                          follow= {this.props.follow}
                          unfollow= {this.props.unfollow}       
                          followingInProgress = {this.props.followingInProgress}
                     /> 
                     </>
      }
  
  }

let  mapStateToProps = (state) => {

return {


     users: getUsers(state),
     pageSize: getPageSize(state),
     followedUsersCount: getFollowedUsersCount(state),
     currentPage: getCurrentPage(state),
     isFetching: getIsFetching(state),
     followingInProgress: getFollowingInProgress(state)
}
}




export default compose (    
     connect (mapStateToProps, {
     follow, 
     unfollow,
     setCurrentPage,// sa action craeter chi,ayl dra ardyunq@
     toggleFollowingProgress,
     requestFollowedUsers
  }),withAuthRedirect) (FriendsContainer);

