
import {authAPI, securityAPI} from '../.vs/src/api/api';
import { stopSubmit } from 'redux-form';

const SET_USER_DATA= 'samurai-network/auth/SET_USER_DATA';

const GET_CAPTCHA_URL_SUCCESS= 'GET_CAPTCHA_URL_SUCCESS';


let initialState = {

userId: null,
email: null,
login: null,
isAuth: false,
captchaUrl: null

};


const authReducer = (state = initialState, action) => {

   switch (action.type) {

      case SET_USER_DATA:
     
          return {
     
              ...state,
              ...action.payload
               
             
            
          }

       case GET_CAPTCHA_URL_SUCCESS:

          return {
           
            ...state,
            ...action.payload
             
          }

          default:
                return state;
      }
    
  }

    
export const setAuthUserData= (userId, email, login, isAuth) =>
({ type: SET_USER_DATA, payload: {userId, email, login, isAuth}});


export const getCaptchaUrlSuccess= (captchaUrl) =>
({ type: GET_CAPTCHA_URL_SUCCESS, payload: {captchaUrl}});


//thunk e sa
export const getAuthUserData = () => async (dispatch) => {


  // await-i mijocov spasum enq promisin, u qani vor ayd 
  // funkcian vori mej sapsum enq promisin petq e lini asynxron,
 //  apa funkciayi dimac grum enq async

       let response = await authAPI.me();
  
      
      if (response.data.resultCode === 0) {
  
        let {id, login, email} = response.data.data;
        dispatch(setAuthUserData(id, email, login, true));
     
     
  }
 
   
}


export const login = (email, password, rememberMe, captcha) => async (dispatch) => {
  

 let response = await authAPI.login(email, password, rememberMe, captcha);

   

    if (response.data.resultCode === 0) {
         
      dispatch(getAuthUserData());
   }
   else  {
      
      if(response.data.resultCode === 10)
      {
        dispatch(GetCaptchaUrl());
// verevi grvac nshanakum e, vor state-i mej kstananq captchaUrl-@:
      }

   let message = response.data.messages.length > 0 ? response.data.messages[0] : "Some error";
   dispatch(stopSubmit("login", {_error: message}));
// dadarecnum en tvyalneri submit@, login-@ formai anunn e

   }
   


}

export const GetCaptchaUrl = () => async (dispatch) => {
  

   const response = await securityAPI.getCaptchaUrl();   
   const captchaUrl = response.data.url;
   dispatch(getCaptchaUrlSuccess(captchaUrl));        
      
  
  }


  
export const logout = () => async (dispatch) => {

   let response = await authAPI.logout ();
       if (response.data.resultCode === 0) {
      dispatch(setAuthUserData(null,null,null, false));
   }
   
 
}
export default authReducer;