
import React from 'react';
import { createField, Input, Textarea } from '../../common/FormsControls/FormsControls';
import { reduxForm, Field } from 'redux-form';








const ProfileMusicForm = (props) => {




  const onMainPhotoSelected1 = (e) => {

   const file1=e.target.value;
    
    
    props.saveMusicSuccess1(file1);
    
    
    
    }


  const onMainPhotoSelected2 = (e) => {

      const file2=e.target.value;

    props.saveMusicSuccess2(file2);
    
    
    

    }

     const onMainPhotoSelected3 = (e) => {

   
   const file3=e.target.value;

    props.saveMusicSuccess3(file3);

    
    
    
    }
    
 
  return  <>

          <h2> Add Your favorite Music </h2>

      
      <div> {<input  type = {"url" } onChange= {onMainPhotoSelected1}  />} </div>
    
      <div> {<input type = {"url"} onChange= {onMainPhotoSelected2}  />} </div>

      <div> {<input type = {"url"} onChange= {onMainPhotoSelected3}  />} </div>
   
      <button onClick={props.activateEditeMode }> Save </button>

  
     </>

}



export default ProfileMusicForm;

