
import {usersAPI, profileAPI} from '../.vs/src/api/api';
import { stopSubmit } from 'redux-form';


const ADD_POST = 'ADD-POST';

const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';
const SAVE_MUSIC_SUCCESS1 = 'SAVE_MUSIC_SUCCESS1';
const SAVE_MUSIC_SUCCESS2 = 'SAVE_MUSIC_SUCCESS2';
const SAVE_MUSIC_SUCCESS3 = 'SAVE_MUSIC_SUCCESS3';


let initialState = {


    posts: [

        {id: 1,  message: 'Hi', likesCount: 13},
        {id: 1,  message: 'Hi', likesCount: 5},
        {id: 1,  message: 'Blabla', likesCount: 7},

    ],

    profile: null,
    status:"",
    music1: null,
    music2: null,
    music3: null

};


const profileReducer = (state= initialState, action) => {

   switch (action.type)  {

case ADD_POST: 
{
    
        let newPost = {
        
            id: 5,
            message: action.newPostText,
            likesCount: 0
          };

        let stateCopy = {...state};
        stateCopy.posts = [...state.posts];
        stateCopy.posts.push (newPost);
        stateCopy.newPostText= '';
   
          return stateCopy;
        }  

    

     
     case SET_USER_PROFILE:  

     {
    
         return { ...state, profile: action.profile};

     }
     case SAVE_MUSIC_SUCCESS1:  

     {
    
         return { ...state, 
          
          
          music1: action.music1};

     }
     case SAVE_MUSIC_SUCCESS2:  

     {
    
         return { ...state, music2: action.music2};

     }
 case SAVE_MUSIC_SUCCESS3:  

     {
    
         return { ...state,  music3: action.music3};

     }
     
     case SET_STATUS:  

     {
    
         return { ...state, status: action.status};

     }
    
      case SAVE_PHOTO_SUCCESS:  

     {
    
         return { ...state, profile: {...state.profile, photos: action.photos}};

     }

    default: 

          return state;
    
    }
  }

export const addPostActionCreator = (newPostText) => {

        return {type: ADD_POST, newPostText}
           
        }
        
export const setUserProfile = (profile) => {
        
      return { type: SET_USER_PROFILE,  profile }
    
}
export const setStatus = (status) => {
        
      return { type: SET_STATUS, status}
    
}

export const savePhotoSuccess = (photos) => {
        
      return { type: SAVE_PHOTO_SUCCESS, photos}
    
}

export const saveMusicSuccess1 = (file1) => {
        
  return { type: SAVE_MUSIC_SUCCESS1, music1: file1    
    
  }

}

export const saveMusicSuccess2 = ( file2) => {
        
  return { type: SAVE_MUSIC_SUCCESS2, music2: file2}

}
export const saveMusicSuccess3 = ( file3) => {
        
  return { type: SAVE_MUSIC_SUCCESS3, music3: file3}

}
export const getUserProfile = (userId) => async (dispatch) => {

let data = await usersAPI.getProfile(userId);
     dispatch(setUserProfile(data));
     
}

export const getStatus = (userId) => async (dispatch) => {
  let data = await profileAPI.getStatus(userId);
     dispatch(setStatus(data));
  
}



export const updateStatus = (status) => async (dispatch) => {
try{
let data = await profileAPI.updateStatus(status);
   if(data.resultCode === 0){
    dispatch(setStatus(status));
}
}

catch(error) {


  
}
  
// try catchov erorn enq hanum kam inch vor ban grum

}

export const savePhoto = (file) => async (dispatch) => {

let data = await profileAPI.savePhoto(file);
   if(data.resultCode === 0){
    dispatch(savePhotoSuccess(data.photos));
}
  
} 

export const saveProfile = (profile) => async (dispatch, getState) => {
// sankai mej dispatchi het galis e naev getState, vor info uni statei masin

  const userId= getState().auth.userId;
  let data = await profileAPI.saveProfile(profile);

  if(data.data.resultCode === 0){
      dispatch(getUserProfile(userId));
  }// aysinqn profil tarmacnum enq informaciayov u getUserProfilin talis enq,
  // vor noric stananq tarmacvac profil@
    
else {

dispatch(stopSubmit ("edit-profile",  { _error: data.data.messages[0]}));
//ete {_error: data.data.messages[0]})-i poxaren greq {"contacts": {"facebook": data.data.messages[0]}}
//apa cuyc kta facebooki dashti sxal mutq@, isk error@ @ndhanurn e cuyc talis

   return Promise.reject(data.data.messages[0]);
}

}



export default profileReducer;