import React from 'react';
import { Input } from '../../common/FormsControls/FormsControls';
import { reduxForm, Field } from 'redux-form';
import s from './SearchUser.module.css';

const SearchUserForm = (props) => {

return <div>

<form onSubmit= {props.handleSubmit} >

   <div>
   
    <label>  <h3> SEARCH USER </h3> </label>
    <Field component= {Input} name= {"searchedName"}  type= {"search"} placeholder= {"Search User by Name"}/> 

    </div>

    <div>

<button className= {s.search}> Search  </button>

  </div>

</form>

</div>


}
const SearchUserReduxForm = reduxForm({form: 'search'})(SearchUserForm);

export default SearchUserReduxForm;