
import {authAPI} from '../.vs/src/api/api';
import { stopSubmit } from 'redux-form';
import { getAuthUserData } from './auth-reducer';

const INITIALIZED_SUCCESS = 'INITIALIZED_SUCCESS';




let initialState = {

initialized: false

};


const appReducer = (state = initialState, action) => {

   switch (action.type) {

      case INITIALIZED_SUCCESS:
     
          return {
     
              ...state, 
              initialized: true
             
          }
          default:
                return state;
      }
    
  }

    
export const initializedSuccess = () =>
({ type: INITIALIZED_SUCCESS});




//thunk e sa
export const initializeApp = () => (dispatch)=> {

    let promise = dispatch(getAuthUserData());

    //dispatch(somethingelse());

  //Promise.all([promise]) //qani vor karox e promisner shat linen gru, enq promisneri massiv
  //ete bolor promisner@ katarvel en, apa nor tox ani mer grvac@, aysinqn stugum en vor dranq katarven
  
  promise.then(() => {

dispatch(initializedSuccess());

   });

}


export default appReducer;